.char-detail {
    display: inline-flex;
    flex-wrap: wrap;
    border-radius: 1rem;
    overflow: hidden;
    justify-content: center;
    background-color: lightgray;
    margin-bottom: 1rem;
}
.char-detail > img {
    width: '350px';
    display: block;
    height: auto;
    align-self:baseline;
    background-color: hsl(100, 0%, 70%);
    
}
.char-info {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    margin: 0%;
    padding: 1rem;
}
.char-info > h1, h2, p {
    margin: 0%;
    padding-top: 0.5rem;
}
.char-detail-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}
.char-detail-link {
    text-decoration: none;
    background-color: lightgray;
    padding: 0.5rem;
    border-radius: 0.5rem;
    width: 15rem;
    text-align: center;
    color: black;
}

.char-detail-link:hover {
    background-color: darkgray;
    font-weight: bold;
}