.char {
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self:flex-start;
    background-color: hsl(100, 0%, 90%);
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    box-shadow: 2px 2px 5px black;
}
.char:hover {
    box-shadow: none;
}
.char:hover > .infoContainer {
    visibility: visible;
    box-shadow: 0px 0px 8px black inset;
}
.char>h3 {
    width: 100%;
    margin: 0;
    color: white;
    padding: 10px 0;
    background-color: hsl(100, 0%, 30%);
    text-align: center;
}
.char>img {
    width: 100%;
    display: block;
    background-color: hsl(100, 0%, 70%)
}


.infoContainer {
    position: absolute;
    background-color: hsla(0, 0%, 100%, .5);
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: black;
    visibility: hidden;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    flex-direction: column;
    font-weight: bold;
    overflow: auto;
    cursor: pointer;
}

.infoContainer > p {
    width: 80%;
    margin: 0%;
    padding: 0%;
    text-align: center;
}
