.infoWrapper {
    text-align: center;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 1rem;
    max-width: 720px;
}
.jumbo {
    padding: 5rem;
    background-color:rgb(0, 0, 206);
    text-align: center;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.flex1 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: hsl(31, 78%, 90%);
    padding: 1rem;
    border-radius: 0.5rem;
}
.flexInfo {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 1rem;
}
.info h2 {
    padding: 2rem 0;
}
.flex2 {
    display: flex;
    gap: 1rem;
    justify-content: center;
    margin-top: 1rem;
    flex-wrap: wrap;
}
.char-links {
    text-decoration: none;
    padding: 1rem;
    border-radius: 1rem;
    background-color: hsl(39, 14%, 47%);
    width: 10rem;
    color: white;
    font-weight: bold;
}
.char-links:hover {

    background-color: hsl(39, 40%, 80%);
    color: black;
}
.infoBtn {
    padding: 1rem;
    height: 3rem;
    border: none;
    width: 15rem;
    font-size: 1rem;
    background-color: white;
}
.infoBtn:hover {
    font-weight: bold;
    background-color: rgb(218, 216, 255);
}
