.gotopage {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 1rem;
}
.gotopage > button {
    background-color: rgb(222, 214, 197);
    border: 1px solid black;
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
}
.gotopage > input {
    padding: 0.5rem;
    background-color: rgb(255, 250, 218);
    border: 1px solid black;
    outline: none;
}