.pagiBtn {
    padding: 0.5rem;
    font-size: 1rem;
    background-color: transparent;
    border-radius: 1rem;
    border: none;
    margin: 1rem;
}

.pagiBtn:hover {
    background-color: hsla(0, 0%, 50%, 0.5);
}

.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.filler {
    width: 100px;
}

.pagination > p, button {
    height: 2rem;
}
