.navbar {
    display: flex;
    background-color: rgb(0, 0, 181);
    padding: 1rem;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2rem;
    padding: 1rem 2rem;
    position: sticky;
    top: 0%;
    box-shadow: 2px 2px 5px black;
    z-index: 10;
}
.navWrapper {
    display: flex;
    align-items: center;
    color: white;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
}
.navLink {
    text-decoration: none;
    color: white;
    width: 8rem;
    text-align: center;
}
.navLink:hover {
    font-weight: bold;
}