.pagenav {
    padding: 0rem;
    display:inline-flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    flex-wrap:wrap;
    border-radius: 1rem;
    overflow: hidden;
    background-color: hsl(213, 8%, 72%);
    margin: 1rem;
}
.page-nav-btn {
    border: none;
    background-color: hsla(0, 0%, 0%, .4);
    font-size: 1rem;
    color: white;
    width: 10rem;
    padding: 1rem;
    height: 100%;
}
.page-nav-btn-disabled {
    border: none;
    background-color: transparent;
    width: 10rem;
}
.page-nav-btn:hover {
    background-color: hsla(0, 0%, 0%, .75);
    cursor: pointer;
}
.pagenavcenter {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}
.pagenavcenter > p {
    height: 2rem;
}